import {Component} from "react";
import '../../styles/base.scss';
import './styles/anchorList.scss';
import {pTx} from "../../utils/ScreenUtil";
import {Link} from "react-keeper";
import {homepage,urlPaths} from "../../config/global"

export default class AnchorList extends Component{

    state = {
        anchorList: []
    }

    searchInput = null;

    anchorList = [];

    componentDidMount() {
        this.anchorList = [
            {id:1,url:"a.webp",title:"Higuchi Kaede"},
            {id:2,url:"b.webp",title:"Shikinagi Akira"},
            {id:3,url:"c.webp",title:"Naraka"},
            {id:4,url:"d.webp",title:"Suo Sango"},
            {id:5,url:"e.webp",title:"Hoshirube Sho"},
            {id:6,url:"f.webp",title:"Nagisa Arcinia"},
            {id:7,url:"g.webp",title:"Dola"},
            {id:8,url:"h.webp",title:"Kenmochi Toya"},
            {id:9,url:"i.webp",title:"Inami Rai"},
            {id:10,url:"j.webp",title:"Yamagami Karuta"}];
        this.setState({anchorList: this.anchorList});
    }

    search = ()=>{
        const searchValue = this.searchInput.value;
        if(searchValue !== undefined && searchValue !== null){
            const oldAnchorList = this.anchorList;
            let newAnchorList = oldAnchorList.filter(t=>{return t.title.indexOf(searchValue) !== -1});
            this.setState({anchorList: newAnchorList});
        }
    }

    render() {
        const {anchorList} = this.state;
        return(
            <div style={{marginTop: pTx(50),paddingBottom: pTx(50)}}
                 className="anchorListContainer clearfix">
                <div style={{width: pTx(345),height: pTx(36),
                    borderRadius: pTx(18)}}
                     className="searchBox">
                    <input ref={(c)=>this.searchInput = c} style={{borderRadius: pTx(18)}} type="text" placeholder="Random"/>
                    <button style={{width: pTx(60),borderRadius: pTx(18)}}
                            className="searchButton" onTouchStart={this.search}>搜索</button>
                </div>
                <div style={{width: pTx(345)}} className="picList">
                    {
                        anchorList.map(t=>{
                            return(
                                <Link to={`${urlPaths.anchorDetail}/${t.id}`} key={t.id}>
                                    <div className="every">
                                        <div style={{width: pTx(118),height: pTx(118),borderRadius: pTx(118/2)}} className="pic">
                                            <img src={`${homepage}/images/anchorList/${t.url}`}/>
                                        </div>
                                        <div className="anchorName">
                                            {t.title}
                                        </div>
                                    </div>
                                </Link>
                            )
                        })
                    }
                </div>
            </div>
        )
    }
}