import {Component} from "react";
import {pTx} from "../../utils/ScreenUtil";
import './styles/container.scss';
import {Fragment} from "react";
import {BrowserRouter, Route} from "react-keeper";
import AnchorList from "../anchorList/AnchorList";
import AnchorDetail from "../anchorDetail/AnchorDetail";
import {homepage,urlPaths} from "../../config/global";

export default class Container extends Component {

    state = {
        menuShow: false,
    }

    render() {
        const {menuShow} = this.state;
        return (
            <Fragment>
                <div style={{fontSize: pTx(20), display: menuShow ? "block" : "none"}} className="menu">
                    <button onTouchStart={() => {
                        this.setState({menuShow: false})
                    }}>
                        ❌
                    </button>
                    <div className="content">
                        <div>主页</div>
                        <a href={urlPaths.anchorList}>
                            <div>主播列表</div>
                        </a>
                        <div>直播日历</div>
                    </div>
                </div>

                <div style={{height: pTx(50)}} className="header">
                    <button onTouchStart={() => {
                        this.setState({menuShow: true})
                    }} style={{
                        width: pTx(30),
                        height: pTx(16), right: pTx(12)
                    }}>
                        <span></span>
                        <span></span>
                        <span></span>
                    </button>
                </div>
                <BrowserRouter>
                    <Fragment>
                        <Route cache path={urlPaths.anchorList} component={AnchorList}/>
                        <Route exact path={urlPaths.anchorDetail+"/:anchorId"} component={AnchorDetail}/>
                        <Route cache path={homepage} component={AnchorList}/>
                    </Fragment>
                </BrowserRouter>
            </Fragment>
        )
    }
}